.map {
    width: 100%;
    height: 400px;
}

.bike-station {
    background-color: chartreuse;
    border: 1px solid black;
    border-radius: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 10px;
}

.bike-station--off {
    background-color: lightgray;
}

.bike-station--empty {
    background-color: red;
}

.bike-station--low {
    background-color: yellow;
}